import { makeStyles } from "@material-ui/core";
import './intro.scss';
import { init } from 'ityped'
import { useRef, useEffect } from "react"
import { Button } from "@mui/material";

const useStyles = makeStyles((theme) => ({
    root: {
        minHeight:"100vh",
        backgroundImage: `url(${process.env.PUBLIC_URL + "/assets/intro.jpg"})`,
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover",
        display: "flex",
        opacity:"0.95",
        objectFit:"cover"
    },
}));

export default function Intro() {
    const classes = useStyles();
    const textRef = useRef();
    useEffect(() => {
        // console.log(textRef)
        init(textRef.current, {
            showCursor: true,
            backDelay: 1500,
            backSpeed: 60,
            strings: ['Application Developer', ".Net Developer"]
        })

    }, []);
    return (
        <div className={classes.root} id="intro">
            <div className="item">
                <div className="wrapper">
                    <h4>Welcome to my World, Brilliant Education.</h4>                     
                    <h1>Cahya Julian</h1>
                    <h3>Junior <span ref={textRef}></span></h3>
                    <Button className="btn" variant="outlined" onClick={() => window.open('/static/media/CahyaJulianCV.pdf')}>Resume</Button>
                    <br/>
                </div>
            </div>
        </div>
    )
}
