import "./contact.scss";
import { makeStyles } from "@material-ui/core";
import { Mail, LinkedIn, PhonelinkRing } from '@material-ui/icons'

const useStyles = makeStyles((theme) => ({
    root: {
        minHeight: "100vh",
        backgroundImage: `url(${process.env.PUBLIC_URL + "/assets/contactunsplash.jpg"})`,
        backgroundRepeat: "no-repeat",
        backgroundSize: "cover",
        display: "flex",
        opacity: "0.95",
        objectFit: "contain"
    },
}));
export default function Contact() {
    const classes = useStyles();
    return (
        <div className={classes.root} id="contact">
            <div className="wrapper">
                <h1>Contact Me</h1>
                <ul>
                    <li>
                        <Mail className="icon">XASA</Mail>
                        <span>cahyajuliannn@gmail.com</span>
                    </li>
                    <li>
                        <LinkedIn className="icon"/>
                        <span>cahya-julian-03b188121</span>
                    </li>
                    <li>
                        <PhonelinkRing className="icon"/>
                        <span>+62821-1804-0401</span>
                    </li>
                </ul>
            </div>
        </div>
    );
}
