import { useState } from "react";
import "./works.scss";

export default function Works() {
  const [currentSlide, setCurrentSlide] = useState(0);
  const data = [
    {
      id: "1",
      icon: "./assets/globe.png",
      title: "Codes",
      desc:
        "I have plenty of repositories in my Github account, I learned from many sources how to codes. You can see my project there. Thanks.",
      img:
        "assets/codeunsplash.jpg",
      links:"https://github.com/cahyajuliannn"
    },
    {
      id: "2",
      icon: "./assets/writing.png",
      title: "Publication",
      desc:
        "I studied physics so I have an experience how to write an article well. I've been collaborated with others researcher from Indonesia and Malaysia and also I feel so gratefull because there's a researcher from Sheffield University, UK in my paper.",
      img:
        "assets/intro.jpg",
      links:"https://www.researchgate.net/profile/Cahya-Julian-2"
    },
  ];

  const handleClick = (way) => {
    way === "left"
      ? setCurrentSlide(currentSlide > 0 ? currentSlide - 1 : 2)
      : setCurrentSlide(currentSlide < data.length - 1 ? currentSlide + 1 : 0);
  };
  
  return (
    <div className="works" id="works">
      <div
        className="slider"
        style={{ transform: `translateX(-${currentSlide * 100}vw)` }}
      >
        {data.map((d) => (
          <div className="container">
            <div className="item">
              <div className="left">
                <div className="leftContainer">
                  <div className="imgContainer">
                    <img src={d.icon} alt="" />
                  </div>
                  <h2>{d.title}</h2>
                  <p>{d.desc}</p>
                  <span onClick={()=> window.open(`${d.links}`, "_blank")}>Projects</span>
                </div>
              </div>
              <div className="right">
                <img
                  src={d.img}
                  alt=""
                />
              </div>
            </div>
          </div>
        ))}
      </div>
      <img
        src="assets/arrow.png"
        className="arrow left"
        alt=""
        onClick={() => handleClick("left")}
      />
      <img
        src="assets/arrow.png"
        className="arrow right"
        alt=""
        onClick={() => handleClick()}
      />
    </div>
  );
}
