import './aboutme.scss'

export default function AboutMe() {
    return (
        <div className="aboutme" id="aboutme">
            <div className="left">
                <div className="imgContainer">
                    <img src="assets/cahyajulian.png" alt="" />
                </div>
            </div>
            <div className="right">
                <div className="wrapper">
                    <h5>"I'm an experienced Junior Application Developer
                        with a demonstrated history of working in the information technology industry.
                        I am able to work well under pressure and adhere to
                        strict deadlines."
                    </h5>                    
                </div>
            </div>
        </div>
    )
}
